<template>
  <div>
    <van-tabbar v-model="active">
      <van-tabbar-item :icon="item.icon"
                       v-for="(item, i) in foots"
                       :key="i"
                       :badge="i==2?num:''"
                       :to='item.path'>{{item.txt}}</van-tabbar-item>

    </van-tabbar>
  </div>
</template>

<script>
import { foots } from '../tools/foots'
// import store from '@/store'
import {
  mapState,
  mapMutations
} from 'vuex'

export default {
  data () {
    return {
      active: 0,
      foots
    };
  },
  computed: {
    ...mapState([
      'num'
    ])
  },
  mounted () {
    console.log(this.active)

    this.foots.forEach((item, index) => {
      if (item.name === this.$route.name) {
        this.active = index
      }
    })
  },
}
</script>
