<template>
  <div>
    <van-nav-bar :title="title"
                 left-arrow
                 @click-left="onClickLeft"
                 @click-right="onClickRight">
      <template #right>
        <!-- #right是具名插槽的缩写方式，就是v-slot:right -->
        <van-icon name="manager-o"
                  size="38"
                  @click='link'
                  v-if="show" />
      </template>
    </van-nav-bar>
  </div>
</template>
<script>
export default {
  data () {
    return {

    }
  },
  props: {
    title: String,
    show: {
      type: Boolean,
      default: true
    }
  }
  ,
  methods: {
    onClickLeft () {
      this.$router.go(-1)
    },
    onClickRight () {

    },
    link () {
      this.$router.push({ name: 'regiest' })
    }
  }
}
</script>
<style lang="scss">
.van-nav-bar {
  background: linear-gradient(to right bottom, #c6ffdd, #f7797d);
  color: white;
}
.van-nav-bar__title {
  color: #4e54c8;
}
</style>