<template>
  <div>
    <input type="file"
           @change='file'
           class="file"
           ref='one'>
    <div class="avatar"
         data-enter-time="1588858244"
         data-click-fun="track_f_94530"><img @click='upload'
           :src="src"
           class="avator-icon">
      <div class="nick-name">立即登录</div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {

      // src: ''
    }
  },
  props: ['src']
  ,
  methods: {
    upload () {
      this.$refs.one.click()
    },
    file () {

      console.log(this.$refs.one.files[0])
      var file1 = this.$refs.one.files[0];
      var data = new FormData();//内置对象 实例化生成一个表单对象
      data.append('avatar', file1);
      data.append('ceshi', 'hahha')
      // console.log(data) 这里不会显示的傻货，在network里可以看到
      this.axios({
        url: '/vue/uploading',
        method: 'POST',
        data: data
      }).then(res => {
        console.log(res)
        this.src = res.data.path.replace(/public/, 'http://localhost:4000')

      })
    }
  },
  mounted () {
    if (localStorage.avatar) {
      this.src = localStorage.avatar.replace(/public/, 'http://localhost:4000')
    } else {
      this.src = ''
    }
  }

}
</script>

<style lang="scss" scoped>
.avatar {
  text-align: center;
  // margin-top: -44px;
  height: 200px;
  padding-left: 22px;

  background: url(https://assets.maizuo.com/h5/v5/public/app/img/bg.6837f67.png);
  background-size: cover;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  color: #fff;
  box-sizing: border-box;
}
.avator-icon {
  width: 63px;
  height: 63px;
  border-radius: 35px;
  margin-right: 20px;
  border: 2px solid #fff;
}
.center-view .avatar .nick-name {
  font-size: 16px;
  box-sizing: border-box;
}

.file {
  display: none;
}
</style>