import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import {
  Toast
} from 'vant'
Vue.use(VueAxios, axios)

let token = '';
axios.defaults.headers.common['token'] = token;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'

function onloading() {
  Toast.clear()
  Toast.loading({
    message: '加载中...',
    forbidClick: true,
    duration: 1000
  });
}

function success(msg) {
  Toast.clear()
  Toast.success({
    message: msg,
    forbidClick: true,
    duration: 600
  })
}

function fail(msg) {
  Toast.clear()
  Toast.fail({
    message: msg,
    forbidClick: true,
    duration: 600
  })
}



axios.interceptors.request.use((config) => {
  token = sessionStorage.token || token;
  config.headers.token = token; //配置传递SessionStorage中的token
  console.log(config.url)

  onloading()
  return config
}, (err) => {
  fail('请求失败')
  return Promise.reject(err)
})
axios.interceptors.response.use((response) => {
  success(response.data.msg || '响应成功')
  return response
}, (err) => {
  fail(response.data.msg || '响应失败')
  return Promise.reject(err)
})
