import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './registerServiceWorker'

import 'lib-flexible' //这是淘宝和postcss-pxtorem插件搭配，适配移动端
import './style/common.scss' //全局样式
import './tools/vant' //引入vant组件
import './tools/globle' //引入底部foot组件全局注册
//引入axios
import './tools/axios'
Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
