import Vue from 'vue'
import Vuex from 'vuex'

import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios)
Vue.use(Vuex)


import {
  NEW
} from './aa'

export default new Vuex.Store({
  state: {
    num: 8,
    cinemas: ''
  },
  mutations: {
    changenum(state) {
      state.num++;
    },
    [NEW](state) {
      state.num++;
    },
    getcinemalist(state, payload) {
      state.cinemas = payload
    }
  },
  actions: {
    getcinemalist({
      commit
    }) {
      var cityId = JSON.parse(localStorage.city).cityId
      //这里不能使用this.axios因为不是组件内使用，都不能使用this.axios等
      axios.get("/zuo/gateway?ticketFlag=1&k=4761833", {
        headers: {
          'X-Host': 'mall.film-ticket.cinema.list'
        },
        params: {
          'cityId': cityId
        },
      }).then(res => {
        commit('getcinemalist', res.data.data.cinemas)
        // this.cinemas = res.data.data.cinemas
        // console.log(res)
      })
    }
  },
  modules: {}
})
